





















































import { Action, State } from 'vuex-class';
import { Component, Vue } from 'vue-property-decorator';
import { INotification } from '@/types/interface';
import banner from '../../components/Banner.vue';
import itemPanel from './ItemPanel.vue';

@Component({
  components: {
    banner,
    itemPanel,
  },
})

export default class MainPage extends Vue {
  @Action('getNotificationList', { namespace: 'notification' }) getNotificationList!: () => Promise<void>;
  @Action('hideNotification', { namespace: 'notification' }) hideNotification!: (id: string) => Promise<void>;
  @State('notificationList', { namespace: 'notification' }) stateNotificationList!: INotification[];

  tabSelected = 'activity';
  unreadActivity = true;
  unreadNews = true;
  list: INotification[] = [];

  created() {
    this.update();
  }

  get activityList() {
    return this.list.filter((d: INotification) => d.group === 0 && !d.hide);
  }

  get articleList() {
    return this.list.filter((d: INotification) => d.group === 5 && !d.hide);
  }

  get showEmpty() {
    if (this.tabSelected === 'activity') return this.activityList.length === 0;
    return this.articleList.length === 0;
  }

  link() {
    this.$router.push('/main');
  }

  switchTab(t: string) {
    this.tabSelected = t;
  }

  update() {
    this.getNotificationList()
      .then(() => {
        this.list = this.stateNotificationList;
      });
  }

  activityContent(id: string) {
    this.$router.push({ path: '/main/news_content', query: { process: 'notification' } });
  }

  newsContent(id: string) {
    this.$router.push({ path: '/main/news_content', query: { process: 'notification' } });
  }

  hideContent(id: string) {
    this.hideNotification(id)
    .then(() => {
      this.update();
    });
  }
}
